<template>
    <div class="contactus">
        {{contactData}}
    </div>
</template>
<script>
export default {
    name:'contactus',
    data(){
        return{
            contactData:''
        }
    },
    mounted(){
        var _this = this;
        if(this.isApp){
            var DVContacts = api.require('DVContacts');
            DVContacts.allContacts(function(ret,err) {
                if (ret) {
                    // alert(JSON.stringify(ret));
                    _this.contactData = JSON.stringify(ret)
                } else {
                    alert(JSON.stringify(err));
                }
            });
        }
    },
    methods:{
        
    }
}
</script>
<style>
    .curinfo{
        padding: 20px;
    }
    .curinfo li{
        padding: 10px 0;
        font-size: 16px;
    }
</style>


